import "./NavBar.scss";
import { NavLink } from "react-router-dom";
import head from "../../assets/profile-circle-svgrepo-com.svg";
import React from 'react';
import Logout from "../Logout/Logout.js"

export default function NavBar() {
  const userJSON = localStorage.getItem('user');
  const user = JSON.parse(userJSON);


  return (
    <header className="header">
      <div className="header__home">
        <NavLink className="header__homelink" to="/"> 
          Paws Along The Way
         </NavLink> 
      </div>

      <div className="header__right">
            <NavLink to='/userpage'> 
              <img className="header__icon" src={head} alt="profile icon"></img>
            </NavLink>
             
         {user?
        (<NavLink to="/logout" className="header__button"> 
              <Logout />
           </NavLink>)
         :( <NavLink to="/login" className="header__button"> 
              <button className="header__button">Login</button>
          </NavLink>)
        }
        </div>
    </header>
  );
}
